/* global
===================================================*/
@font-face {
  font-family: "Cera Pro";
  src: url("../fonts/CeraPro-Medium.woff2") format("woff2"), url("../fonts/CeraPro-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Cera Pro";
  src: url("../fonts/CeraPro-Black.woff2") format("woff2"), url("../fonts/CeraPro-Black.woff") format("woff");
  font-weight: 800;
  font-style: normal;
  font-display: swap;
}
/*============================================================================
	#Global
==============================================================================*/
html {
  box-sizing: border-box;
  height: 100%;
  scroll-behavior: smooth;
}

*,
*:before,
*:after {
  box-sizing: inherit;
  outline: none;
}

body {
  height: 100%;
  background: white;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

img {
  height: auto;
  max-width: 100%;
  vertical-align: top;
}

svg {
  vertical-align: top;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  appearance: none;
  margin: 0;
}

/* typography
===================================================*/
html {
  font-size: 100%;
}

body,
button,
input,
select,
optgroup,
textarea {
  color: #4E4D5E;
  font-family: "Rubik", sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.66;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
}

.typography > *:first-child {
  margin-top: 0;
}
.typography > *:last-child {
  margin-bottom: 0;
}

h1, .h1,
h2, .h2,
h3, .h3,
h3, .h3,
h4,
h5,
h6 {
  font-family: "Rubik", sans-serif;
  font-weight: 500;
  margin: 18px 0 12px;
  color: #4E4D5E;
}

h1, .h1 {
  font-size: 28px;
  line-height: 1.12;
}

h2, .h2 {
  font-size: 26px;
  line-height: 1.12;
}

h3, .h3 {
  font-size: 22px;
  line-height: 1.12;
}

@media (max-width: 767.98px) {
  h1, .h1 {
    font-size: 26px;
  }

  h2, .h2 {
    font-size: 22px;
  }

  h3, .h3 {
    font-size: 20px;
  }
}
@media (max-width: 374px) {
  h1, .h1 {
    font-size: 24px;
  }

  h2, .h2 {
    font-size: 20px;
  }

  h3, .h3 {
    font-size: 18px;
  }
}
p {
  margin: 14px 0 16px;
  line-height: 1.8;
}

a {
  text-decoration: none;
  color: #BA0024;
  transition: color 0.4s cubic-bezier(0.075, 0.82, 0.165, 1);
}
a:hover {
  text-decoration: underline;
  color: #BA0024;
}

strong {
  font-weight: 500;
}

ul, ol {
  list-style-type: none;
  margin: 0 0 16px 0;
  padding: 0 0 0 16px;
}
ul li, ol li {
  position: relative;
  margin: 14px 0 14px;
}
ul li h1, ul li h2, ul li h3, ul li h4, ul li h5, ul li h6, ol li h1, ol li h2, ol li h3, ol li h4, ol li h5, ol li h6 {
  display: inline-block;
  margin: 0;
  color: #4E4D5E;
  font-family: "Rubik", sans-serif;
  font-weight: 500;
}
ul strong, ol strong {
  font-weight: 500;
  color: inherit;
}

ul li {
  padding-left: 16px;
  position: relative;
}
ul li::before {
  position: absolute;
  content: "";
  display: inline-block;
  height: 4px;
  width: 4px;
  left: 0;
  top: calc(50% - 2px);
  border-radius: 100%;
  background: #6a2331;
}

ol {
  counter-reset: section;
}
ol li::before {
  counter-increment: section;
  content: counter(section) ". ";
  font-weight: 400;
  padding-right: 4px;
}

.table-responsive {
  margin: 24px 0;
}
.table-responsive__name {
  font-size: 14px;
  font-weight: 500;
  margin-bottom: 8px;
}
@media (max-width: 767.98px) {
  .table-responsive__name {
    font-size: 12px;
  }
}

table.table {
  width: 100%;
  border-collapse: collapse;
  position: relative;
  overflow: hidden;
  font-size: 14px;
  font-weight: 400;
  line-height: 140%;
}
@media (max-width: 767.98px) {
  table.table {
    font-size: 12px;
  }
}
table.table::before {
  position: absolute;
  content: "";
  top: 0;
  width: 0;
  height: 100%;
  width: 100%;
  pointer-events: none;
}
table.table thead tr {
  background: rgba(0, 0, 0, 0.05);
}
table.table thead tr:hover {
  background: rgba(0, 0, 0, 0.4);
}
table.table th {
  text-align: left;
}
table.table tr {
  background: rgba(0, 0, 0, 0.05);
  transition: all 0.8s cubic-bezier(0.075, 0.82, 0.165, 1);
}
table.table tr:hover {
  background: rgba(0, 0, 0, 0.2);
}
table.table tr:nth-child(odd) {
  background: #B20426;
  color: white;
}
table.table tr:nth-child(odd):hover {
  background: #77041b;
}
table.table td, table.table th {
  padding: 11px 18px;
  text-align: left;
  border-right: 2px solid #F8F2D4;
}
table.table td:last-child, table.table th:last-child {
  border: none;
}
table.table--2col td:first-child {
  width: 30%;
}
@media (max-width: 767.98px) {
  table.table--2col td:first-child {
    width: 20%;
  }
}
@media (max-width: 480px) {
  table.table--2col td:first-child {
    width: auto;
  }
}
table.table--3col td:first-child {
  width: 20%;
}
@media (max-width: 480px) {
  table.table--3col td:first-child {
    width: auto;
  }
}
table.table--yellow td:last-child {
  text-align: right;
  color: #FFE41E;
}
table.table--yellow tr:nth-child(even) td:last-child {
  color: #d0b700;
}

@media (max-width: 575.98px) {
  .table-responsive {
    width: 100%;
    overflow-x: auto;
  }
  .table-responsive table.table {
    min-width: 540px;
    margin-bottom: 8px;
  }
  .table-responsive table.table--yellow {
    min-width: 100%;
  }
}
/* components
===================================================*/
.container {
  width: 100%;
  padding-right: 10px;
  padding-left: 10px;
  margin-right: auto;
  margin-left: auto;
}

@media (min-width: 375px) {
  .container {
    padding-right: 15px;
    padding-left: 15px;
  }
}
@media (min-width: 576px) {
  .container {
    max-width: 540px;
  }
}
@media (min-width: 768px) {
  .container {
    max-width: 720px;
  }
}
@media (min-width: 992px) {
  .container {
    max-width: 960px;
  }
}
@media (min-width: 1200px) {
  .container {
    max-width: 1070px;
  }
}
.header__top {
  padding: 10px 0;
  background: white;
  color: #000000;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.399312);
}
.header__bottom {
  padding: 10px 0;
  background: #BA0024;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.399312);
}
.header__reg-text {
  font-size: 19px;
  line-height: 1.3;
  text-align: right;
}
.header__auth {
  display: grid;
  grid-auto-flow: column;
  justify-content: center;
  align-items: center;
  gap: 18px;
}
.header__logo {
  margin: 0 auto;
  text-align: center;
}

@media (max-width: 1199.98px) {
  .header__reg-text {
    font-size: 18px;
    line-height: 1.22;
  }
  .header__auth {
    gap: 16px;
  }
}
@media (max-width: 991.98px) {
  .header__reg-text {
    font-size: 16px;
  }
  .header__auth {
    gap: 14px;
  }
}
@media (max-width: 767.98px) {
  .header__top, .header__bottom {
    padding: 10px 0;
  }
  .header__reg-text {
    font-size: 14px;
  }
  .header__auth {
    gap: 12px;
  }
}
@media (max-width: 575.98px) {
  .header__reg-text {
    font-size: 12px;
  }
  .header__auth {
    gap: 10px;
  }
}
@media (max-width: 480.98px) {
  .header__reg-text {
    font-size: 10px;
  }
  .header__auth {
    gap: 8px;
  }
}
.menu-section {
  background: #F8F2D4;
  padding: 2px 0;
  margin-bottom: var(--section-spacing);
}

.main-menu {
  position: relative;
  z-index: 1;
}
.main-menu__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.main-menu__list li {
  margin: 0;
  padding: 0;
}
.main-menu__list li::before {
  display: none;
}
.main-menu__link {
  padding: 15px 28px;
  display: block;
  color: #4E4D5E;
  font-family: "Rubik", sans-serif;
  font-size: 20px;
  line-height: 1;
  font-weight: 400;
  text-transform: uppercase;
  transition: all 0.4s cubic-bezier(0.075, 0.82, 0.165, 1);
  text-shadow: 0px 0px 3.86241px rgba(255, 255, 255, 0);
}
.main-menu__link:hover {
  text-decoration: none;
  text-shadow: 0px 0px 3.86241px rgba(255, 255, 255, 0);
}

@media (max-width: 1199.98px) {
  .main-menu__link {
    font-size: 22px;
    padding: 12px 22px;
  }
}
@media (max-width: 991.98px) {
  .main-menu__link {
    font-size: 18px;
    padding: 10px 14px;
  }
}
@media (max-width: 767.98px) {
  .menu-section {
    padding: 10px 0;
  }

  .main-menu__list {
    white-space: nowrap;
    overflow-x: scroll;
  }
  .main-menu__link {
    font-size: 16px;
    padding: 8px 12px;
  }

  ::-webkit-scrollbar {
    -webkit-appearance: none;
  }

  ::-webkit-scrollbar:horizontal {
    height: 2px;
  }

  ::-webkit-scrollbar-thumb {
    background-color: #6a2331;
  }

  ::-webkit-scrollbar-track {
    background-color: transparent;
  }
}
@media (max-width: 575.98px) {
  .menu-section {
    padding: 8px 0;
  }

  .main-menu__link {
    font-size: 14px;
    padding: 6px 10px;
  }
}
.front-slider {
  position: relative;
  z-index: 1;
  overflow: hidden;
  background-color: white;
  background: #4E4D5E;
}
.front-slider__item {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 480px;
  transition: box-shadow 0.4s cubic-bezier(0.075, 0.82, 0.165, 1);
  overflow: hidden;
  transition: all 0.8s cubic-bezier(0.075, 0.82, 0.165, 1);
}
.front-slider__item:hover {
  transform: scale(1.03);
}
.front-slider__item img {
  width: 100%;
  height: 100%;
  display: block;
  object-fit: cover;
}
@media (max-width: 767.98px) {
  .front-slider__item {
    height: auto;
  }
}
@media (min-width: 1920px) {
  .front-slider__item {
    height: auto;
  }
  .front-slider__item picture {
    width: 100%;
  }
}

.section {
  margin: 0 0 var(--section-spacing);
}
.section__header {
  margin-bottom: 30px;
}
.section__title {
  text-align: center;
  font-size: 44px;
  font-family: "Rubik", sans-serif;
  font-weight: 400;
  line-height: 1.2;
  margin: 0;
  text-shadow: 0px 0px 4.50615px rgba(255, 255, 255, 0.75);
}
.section--bonus {
  margin-bottom: calc(var(--section-spacing) + 40px);
}
.section--deco {
  padding: 40px 0 40px;
  background: #F8F2D4;
}

@media (max-width: 1199.98px) {
  .section__header {
    margin-bottom: 30px;
  }
  .section__title {
    font-size: 42px;
  }
}
@media (max-width: 991.98px) {
  .section__header {
    margin-bottom: 30px;
  }
  .section__title {
    font-size: 40px;
  }
}
@media (max-width: 767.98px) {
  .section__header {
    margin-bottom: 25px;
  }
  .section__title {
    font-size: 24px;
  }
}
@media (max-width: 575.98px) {
  .section__header {
    margin-bottom: 20px;
  }
  .section__title {
    font-size: 26px;
  }
}
@media (max-width: 480.98px) {
  .section__title {
    font-size: 20px;
  }
}
@media (max-width: 374px) {
  .section__header {
    margin-bottom: 15px;
  }
  .section__title {
    font-size: 18px;
  }
}
.site {
  overflow: hidden;
}

.site-content--reverse {
  display: flex;
  flex-direction: column-reverse;
}

.overflow {
  overflow: hidden;
}

.hidden {
  display: none;
}

.d-flex {
  display: flex;
}

.flex-column-reverse {
  flex-direction: column-reverse !important;
}

.vawe-deco, .section--deco {
  position: relative;
  z-index: 10;
}
.vawe-deco::before, .section--deco::before, .vawe-deco::after, .section--deco::after {
  content: "";
  position: absolute;
  top: -6px;
  left: 0;
  height: 7px;
  width: 100%;
  background-image: var(--bg-image, url("../img/vawe-menu.png"));
  background-repeat: repeat-x;
  background-position: center;
  background-size: contain;
  z-index: 1;
}
@media (max-width: 991.98px) {
  .vawe-deco::before, .section--deco::before, .vawe-deco::after, .section--deco::after {
    height: 6px;
    top: -5px;
  }
}
@media (max-width: 575.98px) {
  .vawe-deco::before, .section--deco::before, .vawe-deco::after, .section--deco::after {
    height: 5px;
    top: -4px;
  }
}
@media (max-width: 480px) {
  .vawe-deco::before, .section--deco::before, .vawe-deco::after, .section--deco::after {
    height: 4px;
    top: -3px;
  }
}
.vawe-deco::after, .section--deco::after {
  top: auto;
  bottom: -6px;
  transform: scaleY(-1);
}
@media (max-width: 991.98px) {
  .vawe-deco::after, .section--deco::after {
    top: auto;
    bottom: -5px;
  }
}
@media (max-width: 575.98px) {
  .vawe-deco::after, .section--deco::after {
    top: auto;
    bottom: -4px;
  }
}
@media (max-width: 480px) {
  .vawe-deco::after, .section--deco::after {
    top: auto;
    bottom: -3px;
  }
}
.vawe-deco--inset::before {
  top: 0;
