.game-card {
  position: relative;
  padding-top: 100%;
  overflow: hidden;
  border-radius: 15px;
  cursor: pointer;
  box-shadow: 0 0 0 5px #F8F2D4;
}
.game-card__image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.game-card__image::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: #070707;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.4s cubic-bezier(0.075, 0.82, 0.165, 1);
}
.game-card__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.game-card__button, .game-card__control {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -40%);
  opacity: 0;
  z-index: 2;
  filter: drop-shadow(0px 4px 6px rgba(0, 0, 0, 0.4));
  transition: opacity 0.4s cubic-bezier(0.075, 0.82, 0.165, 1), transform 0.4s cubic-bezier(0.075, 0.82, 0.165, 1);
}
.game-card__control {
  display: grid;
  gap: 16px;
  top: auto;
  bottom: 10%;
  transform: translate(-50%, 20%);
}
.game-card:hover .game-card__image::before {
  opacity: 0.6;
}
.game-card:hover .game-card__control,
.game-card:hover .game-card__button {
  opacity: 1;
  transform: translate(-50%, -50%);
}
.game-card:hover .game-card__control {
  transform: translate(-50%, 0%);
}

